import { string, oneOfType, number } from 'prop-types';
import React, { useRef, useState, useCallback, useEffect } from 'react';

const TYPES = [
  { label: 'Any', value: '' },
  { label: 'Full time', value: 'full_time' },
  { label: 'Part-time', value: 'part_time' },
  { label: 'Contract', value: 'contract' },
  { label: 'Internship', value: 'internship' },
];

const PAGE_SIZES = [
  { label: 5, value: 5 },
  { label: 10, value: 10 },
  { label: 15, value: 15 },
  { label: 25, value: 25 },
  { label: 50, value: 50 },
];

const Filter = ({
  keywords: defaultKeywords,
  type: defaultType,
  pageSize: defaultPageSize,
}) => {
  const ref = useRef();
  const [keywords, setKeywords] = useState(defaultKeywords);
  const [type, setType] = useState(defaultType);
  const [pageSize, setPageSize] = useState(defaultPageSize);
  const onChange = useCallback(
    set => ({ target: { value } }) => set(value),
    []
  );
  const renderOption = useCallback(
    ({ label, value }) => (
      <option key={value} value={value}>
        {label}
      </option>
    ),
    []
  );

  useEffect(() => {
    ref.current.focus();
  }, []);

  return (
    <form>
      <fieldset>
        <legend>Something</legend>
        <dl>
          <dt>
            <label htmlFor="keywords">Keywords</label>
          </dt>
          <dd>
            <input
              type="text"
              name="keywords"
              id="keywords"
              value={keywords}
              onChange={onChange(setKeywords)}
              ref={ref}
            />
          </dd>
        </dl>
        <dl>
          <dt>
            <label htmlFor="type">Type</label>
          </dt>
          <dd>
            <select
              name="type"
              id="type"
              value={type}
              onChange={onChange(setType)}
            >
              {TYPES.map(renderOption)}
            </select>
          </dd>
        </dl>
        <dl>
          <dt>
            <label htmlFor="pageSize">Page size</label>
          </dt>
          <dd>
            <select
              name="pageSize"
              id="pageSize"
              value={pageSize}
              onChange={onChange(setPageSize)}
            >
              {PAGE_SIZES.map(renderOption)}
            </select>
          </dd>
        </dl>
        <button type="submit">Submit</button>
      </fieldset>
    </form>
  );
};

Filter.propTypes = {
  keywords: string.isRequired,
  type: string.isRequired,
  pageSize: oneOfType([string.isRequired, number.isRequired]).isRequired,
};

Filter.defaultProps = {};

export default Filter;
